








import { Icon } from '@/components/icon'
import UUIDMixins from '@/components/mixins/uuid'
import { WrapClasses } from '@/types/components'
import { Component, Prop, Mixins, Vue } from 'vue-property-decorator'

@Component({
    name: 'Block',
    components: {
        Icon,
    },
})
export default class Block extends Mixins(UUIDMixins) {
    @Prop({ type: String, default: '' })
    private lang!: string

    @Prop({ type: String, default: '' })
    private content!: string

    @Prop({ type: [String, Array], default() { return [] } })
    private custom!: string | string[]

    private prefixCls: string = 'block'

    private get wrapClasses(): Array<string | WrapClasses> {
        return [
            `${this.prefixCls}-pre`,
        ]
    }

    private get codeWrapClasses(): Array<string | WrapClasses> {
        return [
            `${this.prefixCls}-code`,
            Array.isArray(this.custom) ? this.custom.join(',') : this.custom,
        ]
    }

    private get isSlot(): boolean {
        return !!this.$slots.content
    }
}
